import { Injectable } from '@angular/core';
import {AngularFireDatabase, AngularFireList} from 'angularfire2/database';
import { FormGroup, FormControl, Validators } from '@angular/forms';


@Injectable({
  providedIn: 'root'
})
export class BlogService {
  constructor(private firebase: AngularFireDatabase) { }
  comments: AngularFireList<any>;
  replies: AngularFireList <any>;
  form = new FormGroup({
    $key: new FormControl(null),
    
    fullname: new FormControl('', Validators.required),
    email: new FormControl('', [Validators.required, Validators.email]),
    website: new FormControl(''),
    message: new FormControl('', Validators.required)
  });
  form1 = new FormGroup({
    $key: new FormControl(null),
    fullname1: new FormControl('', Validators.required),
    email1: new FormControl('', [Validators.required, Validators.email]),
    website1: new FormControl(''),
    message1: new FormControl('', Validators.required)
  });
  getComments(){
    console.log('get comments');
    this.comments = this.firebase.list('comments');
    return this.comments.snapshotChanges();
  }

  getReplies(){
    this.replies = this.firebase.list('replies');
    return this.replies.snapshotChanges();
  }

  insertComment(comment){
   
    this.comments.push({
      fullname: comment.fullname,
      email: comment.email,
      website: comment.website,
      message: comment.message
    })
  }

  insertReply(reply){
    this.replies.push({
      fullname: reply.fullname1,
      email: reply.email1,
      website: reply.website1,
      message: reply.message1
    })
  }


}
