import { Injectable } from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {AngularFireDatabase, AngularFireList} from 'angularfire2/database';

@Injectable({
  providedIn: 'root'
})
export class ContactService {
      
  constructor(private firebase: AngularFireDatabase) { }
    contactList: AngularFireList<any>;


  /**  declare var of FormGroup instance and
   in that create an object containing properties of customers 
  */
  form = new FormGroup({
      $key: new FormControl(null), // primary key to uniquely identify each record in firebase
      fullName: new FormControl('', Validators.required),
      email: new FormControl('', [Validators.required, Validators.email]),
      subject: new FormControl(''),
      message: new FormControl('')
  })


  getContactList(){
    this.contactList = this.firebase.list('contactlist');
    return this.contactList.snapshotChanges();
  }

  insertContact(contact){
    this.contactList.push({
        fullName: contact.fullName,
        email: contact.email,
        subject: contact.subject,
        message: contact.message
    })
  }
}
