import { Component, OnInit, AfterViewInit, ViewChild} from '@angular/core';
import {BlogService} from './shared/blog.service';
import { first } from 'rxjs/operators';
import { AngularFireAuth } from 'angularfire2/auth';

@Component({
  selector: 'app-blog-single',
  templateUrl: './blog-single.component.html',
  styleUrls: ['./blog-single.component.css']
})

export class BlogSingleComponent implements OnInit {
  @ViewChild('para1') para1;

 
  constructor(private blogService: BlogService, private afAuth: AngularFireAuth) { }
  submitted: boolean;
  replySubmitted:boolean;
  submittedSuccessfully: boolean;
  replySubmittedSuccessfully: boolean;

  formControls =  this.blogService.form.controls;
  formControls1 =  this.blogService.form1.controls;

  commentArray = [];
  replyArray = [];
  replyIsOn:boolean = false;
  selectedIndex: number;
  togglePanel: any = {};  
  togglePanel1: any = {}; 
  show:boolean = false; 
  register:boolean = false;
  hideForm:boolean = true;



  
  onSubmit(){
    this.submitted = true;
    if(this.blogService.form.valid){
      if(this.blogService.form.get('$key').value == null){        
        this.blogService.insertComment(this.blogService.form.value);
        this.blogService.form.reset();
        this.submittedSuccessfully = true;
        setTimeout(() => {
          this.submittedSuccessfully = false;
        }, 3000);
      }
      this.submitted = false;
    }
  }

  isLoggedIn() {
    return this.afAuth.authState.pipe(first()).toPromise();
 }


  toggle() {
    this.show = !this.show;
  }

  registerToggle(){
    this.register = !this.register;
  }

  onReply(){
    const commentElement = document.createElement('div');
    
    this.replySubmitted = true;
   
    if(this.blogService.form1.valid){
 
      if(this.blogService.form1.get('$key').value == null){    
       
        this.blogService.insertReply(this.blogService.form1.value);
      
        this.para1.nativeElement.appendChild(commentElement);
        this.blogService.form1.reset();
        this.replySubmittedSuccessfully = true;
        setTimeout(() => {
          this.replySubmittedSuccessfully = false;
        }, 3000);
      }
      this.replySubmitted = false;
    }
  this.hideForm = false;
}



  ngOnInit() {
    this.blogService.getComments().subscribe(list => {
      this.commentArray = list.map(item => {
        return {
          $key: item.key,
          ...item.payload.val()
        }
      })
    });

    this.blogService.getReplies().subscribe(list => {
   
      this.replyArray = list.map(item => {
        return {
          $key: item.key,
          ...item.payload.val()
        }
      })
    });

  }


  
}
